article_target(_id){
this.$router.push({name:'articleDetails',query:{id:_id}})
},
returnTop(){//返回顶部
let top = document.querySelector(".index-main").scrollTop;
if(!(top>100)){
return false;
}
if(this.is_top){
this.is_top = false;
const timer = setInterval(()=>{
top-=50;
if(top<0){
clearInterval(timer);
this.is_top = true;
this.is_TopShow = false;
top=0;
}
document.querySelector(".index-main").scrollTop = top;
},30);
}
},

openGrab(){

},
startNew(){
const oUl = document.querySelector("#notice");
oUl.innerHTML += oUl.innerHTML;
let move_num = 0;
setInterval(()=>{
move_num+=-52;
if(Math.abs(oUl.offsetTop)>(oUl.offsetHeight/2-52)){
oUl.style.top = 0;
move_num = -52;
}
this.$startMove(oUl,{
top: move_num
});
}, 3000);
},
handleScroll:function(event){
event.preventDefault();
this.scrolled = window.scrollY;
if(this.scrolled>35){
this.bgfff='';
}else{
this.bgfff='transparent';
}
}
},
